import Vue from 'vue';
import Router from 'vue-router';
import Matches from './views/Matches.vue';
import Stadiums from './views/Stadiums.vue';
import Login from './views/Login.vue';
import MyBets from './views/MyBets.vue';
import PublicBets from './views/PublicBets.vue';
import Standings from './views/Standings.vue';
import firebaseController from './firebaseController';
import * as firebase from "firebase";

Vue.use(Router);

const router = new Router({
    routes: [
        {
            path: '/',
            name: 'matches',
            component: Matches,
            meta: {requiresAuth: true}
        },
        {
            path: '/matches',
            name: 'matches',
            component: Matches,
            meta: {requiresAuth: true}
        },
        {
            path: '/stadiums',
            name: 'stadiums',
            component: Stadiums,
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '/my-bets',
            name: 'myBets',
            component: MyBets,
            meta: {requiresAuth: true}
        },
        {
            path: '/public-bets',
            name: 'publicBets',
            component: PublicBets,
            meta: {requiresAuth: true}
        },
        {
            path: '/standings',
            name: 'standings',
            component: Standings,
            meta: {requiresAuth: true}
        }
    ],
});


router.beforeEach((to, from, next) => {
    let currentUser = firebase.auth().currentUser;
    let requiresAuth = to.matched.some(rec => rec.meta.requiresAuth);

    if (requiresAuth && !currentUser) {
        router.push('/login');
    }

    else {
        if (to.name == 'login') {
            next()
        } else {
            firebaseController.refreshData().then(() => {
                next();
            })
        }
    }
})

export default router;
