<template>
    <tr :class="{locked: locked, betted: betted || tmpBetted}" :id="'match_' + game.id">
        <td :class="gameclass + '--date'" class="text-center moment-date" v-text="viewDate"></td>
        <td :class="homeclass + ' ' + gameclass + '--hometeam'" class="text-right">
            <teamname :teamId="game.home_team"></teamname>
            <label :class="gameclass + '--label'">
                <input v-if="!locked" :id="'match-' + game.name + '-result-home'" data-type="home" type="text"
                       :class="gameclass + '--result'" v-model.number="bets.bet[game.home_team]">
            </label>
        </td>
        <td :class="gameclass + '--spacer'" class="text-center" :title="game.stadium.name">
            <small v-text="'Match ' + game.name"></small>
        </td>
        <td :class="awayclass + ' ' + gameclass + '--awayteam'">
            <label :class="gameclass + '--label'">
                <input v-if="!locked" :id="'match-' + game.name + '-result-away'" data-type="away" type="text"
                       :class="gameclass + '--result'" v-model.number="bets.bet[game.away_team]">
            </label>
            <teamname :teamId="game.away_team"></teamname>

        </td>
        <td>
            <button v-if="!locked && !(betted || tmpBetted)" v-on:click="placeBet">Bet</button>
            <button v-if="!locked && (betted || tmpBetted)" v-on:click="placeBet">Modify</button>
        </td>
    </tr>
</template>

<script>
    import Teamname from './Teamname.vue';
    import functions from '../static';
    import * as moment from 'moment';
    import {Moment} from 'moment';
    import firebaseController from '../firebaseController';

    export default {
        props: {
            id: {
                type: String,
                required: true,
            },
            game: {
                type: '',
                required: true,
            },
            gametype: {
                type: String,
                required: true,
                default: 'groups',
            },
            locked: {
                type: Boolean,
                required: true,
                default: false
            },
            betted:  {
                type: Boolean,
                required: true,
                default: true
            },
        },

        created(){
            if (this.betted){
                firebaseController.getOwnBets().then(res => {
                    Object.values(res).forEach(bet =>{
                        if (bet.game.name === this.game.name){
                            let tmpBet = {};
                            tmpBet = Array.isArray((bet.bet)) ? _.without(bet.bet,undefined, null) : bet.bet;
                            if (Array.isArray(tmpBet)){
                                this.bets.bet[this.game.home_team] = tmpBet[0];
                                this.bets.bet[this.game.away_team] = tmpBet[1];
                            }
                            else{
                                this.bets.bet[this.game.home_team] = tmpBet[this.game.home_team];
                                this.bets.bet[this.game.away_team] = tmpBet[this.game.away_team];
                            }


                        }
                    })

                })
            }
        },
        methods: {


            placeBet() {
                this.bets.game = this.game;
                this.bets.public = false;
                firebaseController.saveBet(this.game.name, this.bets)
                this.tmpBetted = true;
            },

        },
        computed: {
            gameclass() {
                if (this.gametype !== 'groups') {
                    return 'table-knockouts';
                }
                return 'table-' + this.gametype;
            },
            viewDate() {
                return moment(this.game.date).from(new Date());
            },
            titledate() {
                return functions.titleDate(this.game.date);
            },
            finished() {
                return this.game.finished;
            },
            finishedclass() {
                return this.game.finished ? this.gameclass + '--finished' : '';
            },
            homeclass() {
                if (this.game.finished) {
                    if (this.game.homeResult === this.game.awayResult) {
                        return this.gameclass + '--draw';
                    }
                    if (this.game.homeResult > this.game.awayResult) {
                        return this.gameclass + '--winner';
                    }
                    if (this.game.homeResult < this.game.awayResult) {
                        return this.gameclass + '--loser';
                    }
                }
                return '';
            },
            awayclass() {
                if (this.game.finished) {
                    if (this.game.homeResult === this.game.awayResult) {
                        return this.gameclass + '--draw';
                    }
                    if (this.game.homeResult < this.game.awayResult) {
                        return this.gameclass + '--winner';
                    }
                    if (this.game.homeResult > this.game.awayResult) {
                        return this.gameclass + '--loser';
                    }
                }
                return '';
            },
        },
        data() {
            return {
                bets: {bet : {[this.game.home_team]: 0, [this.game.away_team]: 0}},
                tmpBetted: false
            }
        },
        components: {
            Teamname,
        },
    };
</script>

<style>
    .locked {
        opacity: .4;
        background-color: grey;
    }

    .betted {
        background-color: #96d196;
    }
</style>
