<template>
    <article class="col mb-3">
        <div class="card card--group">
            <div class="card-header">Group {{ group.name }}</div>

            <div class="card-footer p-0">
                <table class="table-groups">
                    <tbody :class="[show ? 'tbody--open' : 'tbody--close']">
                    <match v-for="(match, index) in group.matches" :id="group.name" :game="match" :gametype="'groups'"
                           :key="index" :locked="isLocked(match)" :betted="hasBet(match)"></match>
                    </tbody>
                </table>
            </div>
            <!--{{group}}-->
        </div>
    </article>
</template>

<script>
    import GroupModel from '../Model/group';
    import Teamname from './Teamname.vue';
    import Match from './Match.vue';

    export default {
        data() {
            return {
                isShow: false,
            };
        },
        methods: {
            standingpositon(index) {
                return index === 0 ? 'table-success' : index === 1 ? 'table-info' : '';
            },

            isLocked(match) {
                return new Date(match.date).getTime() < new Date().getTime();
            },

            hasBet(match) {
                let returned = false;
                if (this.ownBets)
                    Object.values(this.ownBets).forEach(bet => {
                        if (bet.game.name === match.name) {
                            returned = true;
                            return true;

                        }
                    })
                if (!returned) {
                    return false;
                }
            }
        },
        computed: {
            show() {
                if (!this.withstanding) {
                    return true;
                }
                return this.isShow;
            },
        },
        props: {
            withstanding: {
                type: Boolean,
                required: false,
                default: true,
            },
            group: {
                type: '',
                required: true,
            },
            ownBets: {}
        },
        components: {
            Teamname,
            Match,
        },
    };
</script>
