<template>
    <article class="col mb-3">
        <div class="card card--group">
            <div class="card-header">My bets</div>

            <div class="card-footer p-0">
                <table class="table-groups">
                    <tbody >
                        <fixed-match v-for="(bet, index) in myBets" :game="bet.game" :gametype="'groups'" :key="index" :score="bet"></fixed-match>
                    </tbody>
                </table>
            </div>
            <!--{{group}}-->
        </div>
    </article>
</template>

<script>
    import FixedMatch from "../Components/FixedMatch";
    import firebaseController from "../firebaseController";

    export default {
        name: "my-bets",
        components: {
            FixedMatch,
        },

        mounted(){
            firebaseController.getOwnBets().then(res=>{
                this.myBets = res;
            })
        },

        data() {
            return {
                myBets: {}
            }
        },
    }
</script>

