import firebase from 'firebase'
import _ from 'lodash'

const config = {
    apiKey: "AIzaSyCrSAEPkZvzZ2COWNdFCeBnw2yKkr2nKmg",
    authDomain: "vb18-288ff.firebaseapp.com",
    databaseURL: "https://vb18-288ff.firebaseio.com",
    projectId: "vb18-288ff",
    storageBucket: "vb18-288ff.appspot.com",
    messagingSenderId: "958361084699"
};

export class FirebaseController {
    firebaseApp;
    db;
    data;
    teams;
    users;
    ownBets;
    loaded = false;
    onLogin = false;
    modalCallback;

    constructor() {
        this.firebaseApp = firebase.initializeApp(config);
        this.db = this.firebaseApp.database();
        firebase.auth().onAuthStateChanged(user => {
            if (user){
                this.initData().then(() => {
                    this.initOwnBets().then(() => {
                        this.loaded = true;
                    })
                });
            }
            else{
                console.log('login')
                console.log('login')

                this.onLogin = true;
            }
            console.log('init')


        })
    }

    initData() {

        return this.db.ref('data').once("value", (res) => {
            this.data = res.val();
            this.teams = this.data.teams;
        }, function (err) {
            console.log(err)
        })
    }

    refreshData(){
        return this.initOwnBets();
    }
    initUsers() {
        return this.db.ref('users/').once('value', res => {
            this.users = res.val();
        })
    }

    initOwnBets() {
        const currentUserId = firebase.auth().currentUser.uid;
        return this.db.ref('bets/' + currentUserId).once('value', res => {
            this.ownBets = Array.isArray((res.val())) ? _.without(res.val(),undefined, null) : res.val();
        });
    }

    getTeam(teamId) {
        //TODO unsafe
        return this.teams[teamId - 1]
    }

    getMatches() {
        if (this.data) {
            return Promise.resolve(this.data)
        }
        else {
            return this.initData().then(() => {
                return Promise.resolve(this.data);
            })
        }
        // return this.data;
    }

    saveBet(matchId, bet) {
        const currentUserId = firebase.auth().currentUser.uid;
        if (new Date(bet.game.date).getTime() > new Date().getTime()){
            return this.db.ref('bets/' + currentUserId + "/" + matchId).set(bet);
        }
        else{
            this.showModal();
            return this.db.ref('cheaters/' + currentUserId).set(bet).then(()=> {
                return Promise.reject("szopos");
            })
        }
    }

    getOwnBets() {
        if (this.ownBets) {
            return Promise.resolve(this.ownBets)
        }
        else {
            return this.initOwnBets().then(() => {

                return Promise.resolve(this.ownBets);
            })
        }
    }

    getPublicBets() {
        return this.db.ref('bets').once('value');
    }

    getFirebaseApp() {
        return this.firebaseApp;
    }

    getCurrentUserId() {
        return firebase.auth().currentUser.uid;
    }

    isLoggedIn() {
        return firebase.auth().onAuthStateChanged(user => {
            return (user);
        });
    }

    saveUser() {
        const currentUser = firebase.auth().currentUser;
        return this.firebaseApp.storage().ref('profiles/' + currentUser.uid).getDownloadURL().then(url => {
            return this.db.ref('users/' + currentUser.uid).set({username: currentUser.displayName, url: url, score: 0})
        })
    }

    getUsers() {
        if (this.users) {
            return Promise.resolve(this.users)
        }
        else {
            return this.initUsers().then(() => {
                return Promise.resolve(this.users);
            })
        }
    }

    showModal(){
        this.modalCallback();
    }
}

const fireabseController = new FirebaseController();
export default fireabseController;