<template>

    <span @mouseout="unhover" @mouseover="hover" v-if="isTeam()" :data-id="'team-' + team.id" class="team--name">
        <i class="flag-icon" :class="icon(team)"></i>
        <!--<div> {{isNotTeam()}} </div>-->
        {{team.name}}
    </span>
    <!--<span v-else v-html="team"></span>-->
</template>

<script>
    import TeamModel from '../Model/team';
    import firebaseController from '../firebaseController';

    export default {
        methods: {
            hover(e) {
                const elements = document.querySelectorAll('[data-id="' + e.target.dataset.id + '"]');
                elements.forEach((element) => {
                    element.classList.add('team--name--hover');
                });
            },
            unhover(e) {
                const elements = document.querySelectorAll('[data-id="' + e.target.dataset.id + '"]');
                elements.forEach((element) => {
                    element.classList.remove('team--name--hover');
                });
            },
            icon(team) {
                return 'flag-icon-' + team.iso2;
            },
            isTeam() {
                return typeof this.teamId === 'number' && this.teamId < 33 ;
            },
        },
        computed: {
            team() {
                return firebaseController.getTeam(this.teamId);
            }
        },
        props: {
            teamId: {
                required: true,
            },
        },
    };
</script>
