<template>
    <div>
        <modal></modal>
        <div class="row" v-if="matches.groups">
            <section class="col" v-if="matches && matches.groups">
                <group v-for="(group, index) in matches.groups" :group="group" :key="index" :withstanding=false
                       :ownBets="ownBets"></group>
            </section>
            <section class="col" v-if="matches && matches.knockouts">
                <knockout v-for="(knockout, index) in matches.knockouts" :knockout="knockout" :key="index"
                          :locked=true></knockout>
            </section>
        </div>
    </div>
</template>

<script>
    import Group from './../Components/Group.vue';
    import Knockout from './../Components/Knockout.vue';
    import firebaseController from '../firebaseController';
    import GroupModel from "../Model/group";
    import KnockoutModel from "../Model/knockout"
    import MatchModel from "../Model/match";
    import Modal from './../Components/Modal';

    export default {
        mounted() {
            firebaseController.getOwnBets().then(res => {
                this.ownBets = res;
                firebaseController.getMatches().then(res => {
                    this.matches.groups = res.groups;
                    this.matches.knockouts = res.knockout
                })
            })

        },
        components: {
            Group,
            Knockout,
            Modal
        },

        data() {
            return {
                ownBets: {},
                matches: {groups: GroupModel, knockouts: KnockoutModel}
            }
        }
    };
</script>
