//     },
// });

moment.relativeTimeThreshold('m', 60);
moment.relativeTimeThreshold('d', 3000);
moment.relativeTimeThreshold('h', 24);
let app;
firebase.auth().onAuthStateChanged(function (user) {
    /* eslint-disable no-new */
    if (!app) {
        new Vue({
            router,
            render: (h) => h(App),
        }).$mount('#app');
    }
})

