<template>

    <article class="col mb-3">
        <div class="card card--group">
            <div class="card-header">Standings</div>

            <div class="card-footer p-0">
                <table class="table-groups">
                    <tbody>
                    <tr v-for="user in users">

                        <td>
                            <img :src="user.url">
                        </td>
                        <td class="text-center">
                            Points: {{user.score}}
                        </td>
                    </tr>

                    </tbody>
                </table>
            </div>
            <!--{{group}}-->
        </div>
    </article>


</template>

<script>
    import firebaseController from '../firebaseController';

    export default {
        name: "standings",

        mounted() {
            firebaseController.getUsers().then(res => {
                this.users = res;
                this.users = Object.values(this.users).sort((a, b) => {
                    return (a.score < b.score) ? 1 : ((b.score < a.score) ? -1 : 0);
                });
            })
        },
        data() {
            return {
                users: {},
            }
        },
    }
</script>

<style scoped>
    img {
        height: 30px;
        border-radius: 50%
    }

    tr {
        display: flex;
        justify-content: center;
    }
</style>